.right-side {
  display: flex;
  flex-direction: column;
  height: 95%;
  justify-content: space-evenly;
}
.right-side>h3{
  margin-bottom: 5px;
}
@media screen and (max-width: 1200px) {
  .right-side {
    justify-content: flex-start;
    margin-top: 3rem;
  }
}

@media screen and (max-width: 768px) {
  .right-side {
    width: 100%;
    margin-top: 0;
  }
  .right-side > div {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
